<template>
  <div class="monster-compass">
    <div v-if="isShowCompass" class="compass-box">
      <!-- 林開群 start -->
      <div @click="showLingMap" class="compass-ling">林開群</div>
      <!-- 林開群 end -->
      <img class='compass-logo' src='@/assets/images/compass-scan.png' alt='scan'>
      <div class="compass-head">怪奇羅盤</div>
      <div class="compass-text">請使用地圖 QRcode 前往怪奇地圖</div>
    </div>
    <!-- 林開群 start -->
    <div v-if="isShowLing" class="compass-map-box">
      <img class='compass-map' src='@/assets/images/map.png' alt='map'>
      <img @click="showBsMan" class='compass-ling-pink-spot' src='@/assets/images/pink-spot.png' alt='pink-spot'>
      <div
        v-if="isShowLingMan"
        v-click-outside="hideLingMan"
        class="compass-ling-man"
      >能力商人：</div>
      <img
        v-if="isEnterLing"
        @click="showLingQuestion"
        class='compass-ling-small-spot'
        src='@/assets/images/small-spot.png'
        alt='small-spot'
      >
      <div
        v-if="isShowLingQuestion"
        v-click-outside="hideLingQuestion"
        class="compass-ling-question"
      >
        <div>基隆最可怕的地方？</div>
        <input v-model.trim="lingAnswer" class="compass-ling-question-input" type="text">
        <img @click="confirmLingAnswer" class='compass-ling-go' src='@/assets/images/go.png' alt='go'>
      </div>
    </div>
    <!-- 林開群 end -->
    <img
      v-if="isShowBackMap"
      @click="backToCompass"
      class='compass-back'
      src='@/assets/images/back-to-compass.png'
      alt='back-to-compass'
    >
  </div>
</template>

<script>

export default {
  name: 'MonsterCompass',
  components: {

  },
  data () {
    return {
      isShowCompass: true,
      isShowLing: false,
      isShowBackMap: false,
      isShowLingMan: false,
      isShowLingQuestion: false,
      lingAnswer: '',
      isEnterLing: true
    }
  },
  mounted () {
    document.addEventListener('click', this.autoHide, false)
    // 打贏林開群
    if (this.lingStatus === 'win') {
      this.isEnterLing = false
    }
    // 返回地圖
    if (this.checkoutPoint === 'lingMap') {
      this.showLingMap()
    }
  },
  destroyed () {
    document.removeEventListener('click', this.autoHide, false)
  },
  methods: {
    showLingMap () {
      this.$store.commit('changePoint', '')
      this.isShowCompass = false
      this.isShowLing = true
      this.isShowBackMap = true
    },
    backToCompass () {
      this.isShowCompass = true
      this.isShowBackMap = false
      this.isShowLing = false
    },
    showBsMan () {
      this.isShowLingMan = !this.isShowLingMan
    },
    hideLingMan () {
      this.isShowLingMan = false
    },
    showLingQuestion () {
      if (this.lingStatus === 'lose') {
        this.$router.push({
          path: './haunted-house-fight'
        })
      } else {
        this.isShowLingQuestion = !this.isShowLingQuestion
      }
    },
    hideLingQuestion () {
      this.isShowLingQuestion = false
    },
    confirmLingAnswer () {
      if (this.lingAnswer === '基隆鬼屋' || this.lingAnswer === '鬼屋' || this.lingAnswer === '林開群' || this.lingAnswer === '林開群洋樓') {
        alert('答對了！準備進入關卡。')
        this.$router.push({
          path: './haunted-house'
        })
      } else {
        alert('答錯囉！請再想想。')
      }
    },
    autoHide (evt) {
      if (!this.isShowLingMan) return false
      if (!this.$el.contains(evt.target)) {
        alert('asdf')
        this.isShowLingMan = false
      }
    }
  },
  computed: {
    lingStatus () {
      return this.$store.state.isFight.lingStatus
    },
    checkoutPoint () {
      return this.$store.state.checkoutPoint
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .compass {

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-ling {
        color: #fff;
        font-size: 30px;
        margin-top: 20px;
      }

      &-logo {
        width: 300px;
      }

      &-head {
        font-size: 20px;
        color: #95ff00;
        margin-top: 10px;
      }

      &-text {
        color: #fff;
        margin-top: 4px;
      }

      &-map-box {
        position: relative;
        text-align: center;
      }

      &-map {
        width: 300px;
      }

      &-ling-pink-spot {
        position: absolute;
        left: 100px;
        top: 100px;
        width: 30px;
      }

      &-ling-man {
        position: absolute;
        left: 100px;
        top: 135px;
        background-color: rgba(255, 255, 255, 0.8);
        padding: 30px;
      }

      &-ling-small-spot {
        position: absolute;
        right: 100px;
        bottom: 100px;
        width: 30px;
      }

      &-ling-question {
        width: 250px;
        position: absolute;
        right: 35px;
        bottom: -88px;
        background-color: rgba(255, 255, 255, 0.8);
        padding: 30px;
      }

      &-ling-question-input {
        width: 100%;
        padding: 4px;
        margin-top: 10px;
      }

      &-ling-go {
        width: 30px;
        margin-top: 15px;
      }

      &-back {
        width: 110px;
        position: fixed;
        bottom: 10px;
        margin-left: 32px;
      }
    }
  }

</style>



